.container {
  width: 90%;
  max-width: 1500px;
  height: 100vh;
}

.center-one {
  margin: 0px auto;
}

.container-50 {
  width: 50%;
  padding: 0em 2em;
}

header {
  padding: 3rem 0em;
}

footer {
  padding: 2em 1em;
}

img {
  width: 100%;
  border-radius: 0.4em;
}

.img-opacity {
  opacity: 0.6;
  transition: opacity 0.3s ease;
}
.img-opacity:hover {
  opacity: 1;
}
.img-container {
  width: 70%;
}

.h-100 {
  height: 100%;
}

/*
  logo styles
*/

.logo-component {
  width: 25em;
  margin-bottom: 1em;
  text-align: center;
}

.logo-component > hr {
  margin-bottom: 1em;
  color: rgba(255, 255, 255, 0.788);
}

.logo-component .logo-sup {
  font-family: "Meie Script", Arial, Helvetica, sans-serif;
  font-size: 3em;
}

.logo-component .logo-inf {
  font-size: 1.5em;
  letter-spacing: 0.7em;
}

/*
  button styles
*/

.btn {
  display: inline-block;
  padding: 0.7em 1.3em;
  background: #1a1818;
  text-decoration: none;
  border: none;
  color: #fff;
  margin: 0.5em;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-size: 1rem;
}

.btn:hover {
  box-shadow: 0px 0px 0.7em rgb(255, 255, 255, 0.4);
}

.btn-rounded {
  border-radius: 1.2em;
}

.btn-rounded-sm {
  padding: 0.7em 1em;
  border-radius: 0.8em;
}

.btn-close {
  color: #cc4747;
}

/*
  social
*/
.instagram {
  color: #f15353;
}

.facebook {
  color: #2caadb;
}
.instagram:hover {
  box-shadow: 0px 0px 0.7em #cc4747;
}

.facebook:hover {
  box-shadow: 0px 0px 0.7em #1d799e;
}

/*
  flexbox
*/
.float-right {
  float: right;
}

.d-block {
  display: block;
}

.d-flex {
  display: flex;
}

.align-items-center {
  align-items: center;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-center {
  justify-content: center;
}

.justify-content-space-around {
  justify-content: space-around;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-column {
  flex-direction: column;
}

.title {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 700;
  letter-spacing: 0.2em;
  font-size: 2.8rem;
}

.text {
  font-size: 1.2em;
  line-height: 1.7em;
}

.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 0.5em;
}

.mb-4 {
  margin-bottom: 4em;
}

.mt-1 {
  margin-top: 2em;
}

.my-1 {
  margin: 1em 0em;
}

.mt-2 {
  margin-top: 3em;
}

.card {
  width: 16em;
  overflow: hidden;
  margin: 0.6em;
}

.card-dark {
  background: #1a1818;
  padding: 1em;
  border-radius: 0.5em;
  margin: 1em;
  width: 25em;
  color: #f7f3f3;
}

.card-dark > h2 {
  padding: 0.2em;
  font-family: "Meie Script", Arial, Helvetica, sans-serif;
  border-bottom: solid 1px #9b9b9b;
  font-size: 2em;
}

.modal {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  justify-content: center;
  align-items: center;
  transition: background 0.3s ease-in;
}

.modal-show {
  display: flex;
}
.modal-header {
  padding-bottom: 0.5em;
  border-bottom: solid 1px #d6d3d3;
  margin-bottom: 0.5em;
}
.modal-content {
  background: #1a1818;
  padding: 1em;
  border-radius: 0.5em;
  color: #fff;
  max-width: 30em;
  width: 100%;
  overflow: hidden;
}

.awssld__content {
  background: none !important;
}

.cursor-pointer {
  cursor: pointer;
}

.media {
  width: 100%;
  height: 26em;
}

@media screen and (max-width: 950px) {
  .container-50 {
    width: 100%;
    padding: 1em 0em;
  }
}

@media screen and (max-width: 815px) {
  .header {
    justify-content: center;
  }
}

@media screen and (max-width: 610px) {
  .img-container {
    width: 90%;
  }
  .card-dark {
    width: 100%;
  }

  .media {
    height: 22em;
  }
  .mb-4 {
    margin-bottom: 2em;
  }
}

@media screen and (max-width: 400px) {
  .logo-component .logo-sup {
    font-size: 2.5em;
  }

  .img-container {
    width: 100%;
  }

  .logo-component .logo-inf {
    font-size: 1.2em;
  }
  .media {
    height: 18em;
  }

  .mb-4 {
    margin-bottom: 1em;
  }
}
