@import url("https://fonts.googleapis.com/css2?family=Meie+Script&family=Roboto:ital,wght@0,400;0,500;1,300&display=swap");

* {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background: rgb(0, 0, 0);
  background-image: linear-gradient(
      90deg,
      rgba(0, 0, 0, 0.9) 100%,
      rgba(0, 0, 0, 0.9) 100%
    ),
    url("./assets/image/thumb.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  color: #fff;
  height: 100vh;
}
